import { useParams } from "react-router-dom"
import { ConstTranslate } from "../../constants/translate"
import { Breadcrumb, Preloader } from "../../components"
import { Controller, useForm } from "react-hook-form";
import { Form } from "react-bootstrap";
import LoadingButton from "../../components/Core-Components/LoadingButton/LoadingButton";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { agentMessageService } from "../../services/agentMessage.service";
import cogoToast from "@hasanm95/cogo-toast";
import { addAgentMessage } from "../../store/user/user-action";
import { ConstMessages, ConstPosition } from "../../constants/messages";


const AgentMessageDetails = () => {
    const { categories, jobTitles } = useSelector(state => state.categoriesManager)
    const { currentUser } = useSelector(state => state.user)

    const [isLoading, setIsLoading] = useState(false)
    const [isLoadInit, setIsLoadInit] = useState(false)
    const [agentMessage, setAgentMessage] = useState(null)
    const { id: contactId } = useParams()
    const { register, handleSubmit, formState: { errors }, setValue, control } = useForm();
    const dispatch = useDispatch()

    useEffect(() => {
        loadAgentMessage()
    }, [])

    useEffect(() => {
        if (agentMessage) loadFormValues()
    }, [agentMessage, setValue])

    const loadAgentMessage = async () => {
        const userAgentMessage = currentUser.agentMessages.find(message => message.contact._id === contactId)
        if (userAgentMessage) {
            return setAgentMessage(userAgentMessage)
        }

        try {
            setIsLoadInit(true)
            const agentMessage = await agentMessageService.getAgentMessageByContactId(contactId)
            if (agentMessage) setAgentMessage(agentMessage)
        } catch (error) {
            cogoToast.error("Cannot get message", { position: ConstPosition.TOP_CENTER });
        } finally {
            setIsLoadInit(false)
        }
    }

    const loadFormValues = () => {
        const defaultValues = {
            userLinkedInUrl: agentMessage.userLinkedInUrl,
            userCompany: agentMessage.userCompany,
            userJobTitle: agentMessage.userJobTitle,
            userCategory: agentMessage.userCategory,
            message: agentMessage.message
        }
        Object.keys(defaultValues).forEach((key) => {
            setValue(key, defaultValues[key]);
        });
    }

    const onSubmit = async (data) => {
        if (Object.keys(errors).length) {
            return cogoToast.error(ConstMessages.PLEASE_FILL_OUT_ALL_THE_FIELDS, { position: ConstPosition.TOP_CENTER });
        }

        setIsLoading(true)
        try {
            const agentMessage = await addAgentMessage(dispatch, { ...data, contactId })
            setAgentMessage(agentMessage)
            cogoToast.success(ConstMessages.MESSAGE_SENT, { position: ConstPosition.TOP_CENTER });
        } catch (error) {
            cogoToast.error(ConstMessages.CANNOT_SEND_MESSAGE, { position: ConstPosition.TOP_CENTER });
        } finally {
            setIsLoading(true)
        }
    }

    const isDisabled = () => {
        return agentMessage ? true : false
    }

    if (isLoadInit) return <Preloader />
    return (
        <div className="body-wrapper space-pb--120 backgrounds__white-top">
            <Breadcrumb pageTitle={ConstTranslate.COMMUNICATE_WITH_AGENT} />
            <form onSubmit={handleSubmit(onSubmit)}>
                <div className="flex gap--15 f-dir-col mx-4">

                    {!isDisabled() && <div className={`input-container${errors?.userLinkedInUrl ? " input-error" : ""}`}>
                        <input
                            className="input"
                            type="text"
                            disabled={isDisabled()}
                            placeholder="LinkedIn Profile"
                            {...register("userLinkedInUrl", { required: true })} />
                    </div>}

                    <div className={`input-container${errors?.userCompany ? " input-error" : ""}`}>
                        <input
                            className="input"
                            type="text"
                            disabled={isDisabled()}
                            placeholder="Your Company Name"
                            {...register("userCompany", { required: true })} />
                    </div>

                    <div className={`select-container${errors?.userCategory ? " error" : ""}`}>
                        <Controller
                            control={control}
                            name="userJobTitle"
                            rules={{ required: true }}
                            render={({ field }) => {
                                return (
                                    <Form.Select disabled={isDisabled()} className="select" {...field}>
                                        <option>{ConstTranslate.YOUR_JOB_TITLE}</option>
                                        {
                                            jobTitles.map(jobTitle => {
                                                return (
                                                    <option key={jobTitle._id} value={jobTitle.title}>{jobTitle.title}</option>
                                                )
                                            })
                                        }
                                    </Form.Select>
                                )
                            }} />
                    </div>

                    <div className={`select-container${errors?.userCategory ? " error" : ""}`}>
                        <Controller
                            control={control}
                            name="userCategory"
                            rules={{ required: true }}
                            render={({ field }) => {
                                return (
                                    <Form.Select disabled={isDisabled()} className="select" {...field}>
                                        <option>Looking for agent in the field of</option>
                                        {
                                            categories.map(category => {
                                                return (
                                                    <option key={category._id} value={category.cat}>{category.title}</option>
                                                )
                                            })
                                        }
                                    </Form.Select>
                                )
                            }} />
                    </div>

                    <div className={`input-container${errors?.message ? " input-error" : ""}`}>
                        <label htmlFor="message" className="input-container__label">Your message:</label>
                        <textarea id="message" className="input" disabled={isDisabled()} rows="4" cols="50" {...register("message", { required: true })}></textarea>
                    </div>

                    {
                        !agentMessage &&
                        <LoadingButton className={"button space-mt--30 __h-50"} isLoading={isLoading}>
                            {ConstTranslate.SEND}
                        </LoadingButton>
                    }
                </div>
            </form>
        </div>
    )
}

export default AgentMessageDetails